

#admin .stock {
    float: left;
    
    padding: max(0.2vw, 0.2vh, 2px) max(1vw, 1vh, 10px);
    border-radius: 4px;
    margin-right: max(6vw, 6vh, 60px);

    transform: translateY(min(-1.2vw, -1.2vh, -12px));
}

.stock.warn {
    background-color: orange;
}
.stock.alert {
    background-color: red;
    animation-name: stock-alert;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}
@keyframes stock-alert {
    0% {
        background-color: red;
    }
    25% {
        background-color: red;
    }
    75% {
        background-color: orange;
    }
    100% {
        background-color: orange;
    }
}
.stock.crisis {
    color: black;
    animation-name: stock-crisis;
    animation-duration: 0.2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    border-width: 4px;
    border-style: solid;
    border-color: black;
}
@keyframes stock-crisis {
    from {
        background-color: darkred;
    }
    to {
        background-color: red;
    }
}
